package material_components.stylesheets

import kotlinx.css.*
import styled.StyleSheet

object TypeScaleStyleSheet: StyleSheet("TypeScaleStyleSheet") {

    private val ff by css {
        fontFamily = "Roboto, Arial, sans-serif"
    }

    val headline1 by css {
        fontSize = 96.px
        fontWeight = FontWeight.lighter
        letterSpacing = (-1.5).px
        +ff
    }

    val headline2 by css {
        fontSize = 60.px
        fontWeight = FontWeight.lighter
        letterSpacing = (-0.5).px
        +ff
    }

    val headline3 by css {
        fontSize = 48.px
        fontWeight = FontWeight.normal
        letterSpacing = 0.px
        +ff
    }
    val headline4 by css {
        fontSize = 34.px
        fontWeight = FontWeight.normal
        letterSpacing = 0.25.px
        +ff
    }
}