package material_components

import whether
import kotlinx.css.*
import kotlinx.html.InputType
import kotlinx.html.js.onChangeFunction
import kotlinx.html.js.onClickFunction
import material_components.stylesheets.ACCENT_COLOR_VAR
import material_components.stylesheets.InputStyleSheet
import material_components.stylesheets.TextDirection
import material_components.stylesheets.TextFieldType
import org.w3c.dom.HTMLInputElement
import org.w3c.dom.events.Event
import react.*
import react.dom.attrs
import styled.*
import kotlin.js.RegExp


val outlinedTextField = functionalComponent<OutlinedTextFieldProps> { props ->
    
    val inputRef = useRef<HTMLInputElement>()
    val containerOnClick = { _: Event ->
        inputRef.current?.focus() ?: Unit
    }
    
    styledDiv {
        
        css {
            +InputStyleSheet.outlinedTextField_container
            if (!props.helperText.isNullOrBlank())
                +InputStyleSheet.outlinedTextField_container_withHelper
            
            props.accentColor?.let {
                setCustomProperty(ACCENT_COLOR_VAR, it)
            }
        }
        
        attrs.onClickFunction = containerOnClick
        
        props.prefixText
            .whether { it.isNotBlank() }
            ?.let { prefixString ->
                styledSpan {  /// Prefix Text
                    
                    css { +InputStyleSheet.outlinedTextField_prefixText }
                    +prefixString
                    
                }
            }
        
        styledInput {  /// Input Logic Element
            
            ref = inputRef
            
            css {
                +InputStyleSheet.outlinedTextField_input
                if (props.textDirection == TextDirection.toRight) textAlign = TextAlign.right
            }
            attrs {
                placeholder = " "
                props.defaultValue?.let { value = it }
                
                type = when (props.textFieldType) {
                    TextFieldType.email -> InputType.email
                    TextFieldType.number -> InputType.number
                    TextFieldType.password -> InputType.password
                    TextFieldType.tel -> InputType.tel
                    TextFieldType.text, null, undefined -> InputType.text
                }
                
                props.minValue?.let { min = "$it" }
                props.maxValue?.let { max = "$it" }
                props.step?.let { step = "$it" }
                props.minLength?.let { minLength = "$it" }
                props.maxLength?.let { maxLength = "$it" }
                required = when (props.isRequired) {
                    true -> true
                    false, null, undefined -> false
                }
                props.pattern?.let { pattern = "$it" }
                disabled = when (props.isDisabled) {
                    true -> true
                    false, null, undefined -> false
                }
                
                props.inputValueChangeHandler?.let { onChangeFunction = it }
            }
        }
        
        props.postfixText
            .whether { it.isNotBlank() }
            ?.let { postfixString ->
                styledSpan {  /// Postfix Text
                    
                    css { +InputStyleSheet.outlinedTextField_postfixText }
                    +postfixString
                    
                }
            }
        
        styledDiv {  /// Outline Decoration
            
            css { +InputStyleSheet.outlinedTextField_outerDecoration }
            
            styledDiv {  /// Decoration before Label cutting outline
                
                css { +InputStyleSheet.outlinedTextField_leftDecoration }
            }
            
            styledDiv {  /// Decoration that allows Label cutting outline
                
                css { +InputStyleSheet.outlinedTextField_centerDecoration }
                
                if (props.labelText.isNotBlank()) styledLabel {  /// Input Label
                    
                    css { +InputStyleSheet.outlinedTextField_labelText }
                    
                    +props.labelText
                }
            }
            
            styledDiv {  /// Decoration after Label cutting outline
                
                css { +InputStyleSheet.outlinedTextField_rightDecoration }
            }
        }
        
        if (!props.helperText.isNullOrBlank()) styledSpan {  /// Helper text
            
            css { +InputStyleSheet.outlinedTextField_helperText }
            
            +(props.helperText ?: "")
        }
    }
}

external interface OutlinedTextFieldProps: RProps {
    var minValue: Number?
    var maxValue: Number?
    var step: Number?
    var minLength: Int?
    var maxLength: Int?
    var isRequired: Boolean?
    var pattern: RegExp?
    var textFieldType: TextFieldType?
    
    var isDisabled: Boolean?
    var textDirection: TextDirection?
    var prefixText: String?
    var postfixText: String?
    var helperText: String?
    var labelText: String
    var defaultValue: String?
    
    var accentColor: Color?
    
    var inputValueChangeHandler: ((Event) -> Unit)?
}

fun RBuilder.outlinedTextField(
    labelText: String,
    handler: RHandler<OutlinedTextFieldProps>
) = child(outlinedTextField) {
    attrs.labelText = labelText
    handler()
}
