object Colors {
    val white__type_high_emphasis = rgba(255, 255, 255, 1.0);
    val white__type_medium_emphasis = rgba(255, 255, 255, .74);
    val white__type_disabled = rgba(255, 255, 255, .54);
    val white__type_low_emphasis = rgba(255, 255, 255, .38);
    val white__tin_focus = rgba(255, 255, 255, .26);
    val white__tin_hover = rgba(255, 255, 255, .07);

    val black__type_high_emphasis = rgba(0, 0, 0, .87);
    val black__type_medium_emphasis = rgba(0, 0, 0, .60);
    val black__type_disabled = rgba(0, 0, 0, .38);
    val black__type_low_emphasis = rgba(0, 0, 0, .24);
    val black__tin_focus = rgba(0, 0, 0, .16);
