fun CSSBuilder.allUnset() = put("all", "unset")

var CSSBuilder.colorVar: String
    get() { error("write-only property") }
    set(value) {
        put("color", value.toCustomProperty())
    }

var CSSBuilder.borderColorVar: String
    get() { error("write-only property") }
    set(value) {
        put("border-color", value.toCustomProperty())
    }

inline fun <S: StyleSheet> ruleSetSelector(styleSheet: S, ruleSet: S.() -> KCallable<RuleSet>): String {
    return "${styleSheet.name}-${ruleSet(styleSheet).name}"
}

fun <T: StyleSheet> T.getDataName(getClass: (T) -> KProperty0<RuleSet>): String {
    return getClassName(getClass).dataStyle
}

inline val String.dataStyle: String
    get() = "[data-style~=$this]"