import kotlinx.browser.window
import kotlinx.css.*
import material_components.stylesheets.TextDirection
import material_components.stylesheets.TextFieldType
import material_components.outlinedTextField
import material_components.stylesheets.TypeScaleStyleSheet
import org.w3c.dom.HTMLInputElement
import org.w3c.dom.events.Event
import react.*
import styled.css
import styled.styledH1
import styled.styledH4
import kotlin.math.*

val calculator = functionalComponent<CalculatorProps> { props ->

    val initSlope = props.slope?.toDoubleOrNull()
    val initSpacing = props.spacing?.toDoubleOrNull()

    var calculatorState by useState<CalculatorState>(object: CalculatorState {
        override var slope = initSlope ?: Double.NaN
        override var spacing = initSpacing ?: Double.NaN

    })

    val handleSpacingChange = { ev: Event ->
        (ev.target as HTMLInputElement).let {
            if (it.reportValidity() && it.value != "") {
                //calculatorState.spacing = it.value.toInt()
                calculatorState = object: CalculatorState {
                    override var slope = calculatorState.slope
                    override var spacing = it.value.toDouble()
                }
            } else if (it.value == "") {
                calculatorState = object: CalculatorState {
                    override var slope = calculatorState.slope
                    override var spacing = Double.NaN
                }
            }
        }
    }

    val handleSlopeChange = { ev: Event ->
        (ev.target as HTMLInputElement).let {
            if (it.reportValidity() && it.value != "") {
                //calculatorState.spacing = it.value.toInt()
                calculatorState = object: CalculatorState {
                    override var slope = it.value.toDouble()
                    override var spacing = calculatorState.spacing
                }
            } else if (it.value == "") {
                calculatorState = object: CalculatorState {
                    override var slope = Double.NaN
                    override var spacing = calculatorState.spacing
                }
            }
        }
    }

    outlinedTextField("Spadek") {
        attrs {
            helperText = "Różnica wysokości słupów"
            accentColor = hex(0x33691e)
            textDirection = TextDirection.toRight
            postfixText = " cm"
            textFieldType = TextFieldType.number
            minValue = 0

            initSlope?.let { defaultValue = "$it" }

            inputValueChangeHandler = handleSlopeChange
        }
    }

    outlinedTextField("Rozstaw") {
        attrs {
            helperText = "Osiowa odległość słupów"
            accentColor = hex(0x33691e)
            textDirection = TextDirection.toRight
            postfixText = " cm"
            textFieldType = TextFieldType.number
            minValue = 0

            initSpacing?.let { defaultValue = "$it" }

            inputValueChangeHandler = handleSpacingChange
        }
    }

    styledH4 {
        css {
            +TypeScaleStyleSheet.headline4
            textAlign = TextAlign.right
            padding(0.px, 16.px)
        }
        when {
            calculatorState.slope.isNaN() -> +"Podaj spadek"
            calculatorState.spacing.isNaN() -> +"Podaj rozstaw"
            abs(calculatorState.slope / calculatorState.spacing) > 1 ->
                +"Spadek nie może być większy od rozstawu"
            else -> css.display = Display.none
        }
    }

    styledH1 {

        css {
            +TypeScaleStyleSheet.headline1
            textAlign = TextAlign.right
            padding(0.px, 16.px)
        }
    
        (asin(calculatorState.slope / sqrt(calculatorState.spacing.pow(2) + calculatorState.slope.pow(2))) * (180 / PI)).whether {
//        (90 - acos(calculatorState.slope / calculatorState.spacing) * (180 / PI)).whether {
            !it.isNaN()
        }?.let {
            +"${it.asDynamic().toFixed(2)}°"
        } ?: run {
            css.display = Display.none
        }

//        +when {
//            calculatorState.slope == -1 -> "Podaj spadek"
//            calculatorState.spacing == -1 -> "Podaj rozstaw"
//            abs(calculatorState.slope.toDouble() / calculatorState.spacing) > 1 ->
//                "Spadek nie może być większy od rozstawu"
//            else -> "${
//                (90 - acos(calculatorState.slope.toDouble() / calculatorState.spacing) * (180 / PI))
//                    .asDynamic()
//                    .toFixed(2)
//            }°"
//        }
    }
}

/**
 * Props for calculator
 */
external interface CalculatorProps: RProps {
    var slope: String?
    var spacing: String?

}

external interface CalculatorState: RState {
    var slope: Double
    var spacing: Double
}
