/////////////////////////
//
//  Contract validation
//
/////////////////////

/**
 * Returns `true` if this nullable object value is `null`.
 */
@OptIn(ExperimentalContracts::class)
fun Any?.isNull(): Boolean {
    contract {
        returns(false) implies (this@isNull != null)
    }

    return this == null
}

/**
 * Returns true if `this` nullable object value is not `null`.
 */
@OptIn(ExperimentalContracts::class)
fun Any?.isNotNull(): Boolean {
    contract {
        returns(false) implies (this@isNotNull == null)
    }

    return this != null
}

/**
 * Validates `this` nullable object as not `null`.
 * Calling this method should explicitly tell that `this` object was already validated prior to this call, especially
 * in another method, that as of `Kotlin 1.4.21` cannot smart-cast fields of classes
 */
@OptIn(ExperimentalContracts::class)
fun Any?.valid() {
    contract {
        returns() implies (this@valid != null)
    }
}

/**
 * Validates `this` nullable object as not `null` and returns it.
 * Calling this method should explicitly tell that `this` object was already validated prior to this call, especially
 * in another method, that as of `Kotlin 1.4.21` cannot smart-cast fields of classes
 */
@OptIn(ExperimentalContracts::class)
fun <T> T?.validated(): T {
    contract {
        returns() implies (this@validated != null)
    }

    return this!!
}

/**
 * Parameters marked with this annotation are assumed to be null-free validated, and any field in those parameters are
 * used with !! operator (or [validated]), thus it's a caller responsibility to check for nullability beforehand.
 */
@Target(AnnotationTarget.VALUE_PARAMETER)
@Retention(AnnotationRetention.RUNTIME)
@MustBeDocumented
annotation class NotNullValidated

/**
 * Returns value of [T] when such value is not equal to null, and passes [condition], otherwise returns `null`.
 * This method allows more one-line / chained code like:
 * ```
 *     myString.whether(String::isNotBlank)?.let { /* something */ }
 * ```
 * instead of:
 * ```
 *     if (!myString.isNullOrBlank) { /* something */ }
 * ```
 * @param condition additional evaluation performed on [T]. It should be noted that in block will not be performed if
 *        null-check is failed, therefore [T] will be casted to not-null.
 */
@OptIn(ExperimentalContracts::class)
fun <T> T?.whether(condition: (T) -> Boolean): T? {
    contract {
        returnsNotNull() implies (this@whether != null)
    }

    return when {
        this.isNull() -> null
        !condition(this) -> null
        else -> this
    }
}

