//  Numbers
//
/////////

/**
 * Returns `this` number if
 * [bounds.start][ClosedRange.start] < `this` < [bounds.endInclusive][ClosedRange.endInclusive],
 * [bounds.start][ClosedRange.start] if `this` is smaller, or [bounds.endInclusive][ClosedRange.endInclusive] if `this`
 * is larger
 */
infix fun <N> Number.boundIn(bounds: ClosedRange<N>): Double
        where N: Number, N: Comparable<N> = this.toDouble().let { value ->
    (bounds.start.toDouble()..bounds.endInclusive.toDouble()).let { range ->
        when {
            value in range -> value
            value < range.start -> range.start
            value > range.endInclusive -> range.endInclusive
            else -> Double.NaN
        }
    }
}

/**
 * Returns array containing positional notation of `this` number, ignoring sign of number.
 */
val Int.positional: IntArray
    get() {
        if (this == 0) return intArrayOf(0)
        val arr = IntArray(9)
        var i = 0
        var n = this
        while (n != 0) {
            arr[i++] = n % 10
            n /= 10
        }
        return arr.sliceArray(0 until i).reversedArray()
    }

/**
 * Returns array containing positional notation of `this` number, ignoring sign of number.
 */
val Long.positional: LongArray
    get() {
        if (this == 0L) return longArrayOf(0L)
        val arr = LongArray(19)
        var i = 0
        var n = this
        while (n != 0L) {
            arr[i++] = n % 10
            n /= 10
        }
        return arr.sliceArray(0 until i).reversedArray()
    }


/////////////////
//
//  Collections
//
/////////////

/**
 * Swaps [i] and [j] indices in this array.
 */
fun <T> Array<T>.swap(i: Int, j: Int) {
    val tmp = this[i]
    this[i] = this[j]
    this[j] = tmp
}

/**
 * Returns first element.
 * @throws [NoSuchElementException] if the list is empty or only one element.
 */

fun <T> List<T>.second(): T {
    if (isEmpty()) throw NoSuchElementException("List is empty.")
    if (size == 1) throw NoSuchElementException("List is one element.")
    return this[1]
}

/**
 * Returns a new mutable map containing all key-value pairs from the given collection of pairs.
 * The returned map preserves the entry iteration order of the original collection.
 * If any of two pairs would have the same key the last one gets added to the map.
 */
fun <K, V> Iterable<Pair<K, V>>.toMutableMap() = this.toMap().toMutableMap()

/**
 * Returns first element.
 * @throws [NoSuchElementException] if the list is empty or only one element.
 */

fun <T> Iterable<T>.second(): T = when (this) {
    is List -> this.second()
    else -> {
        val iterator = iterator()
        if (!iterator.hasNext())
            throw NoSuchElementException("Collection is empty.")
        else {
            iterator.next()
            if (!iterator.hasNext())
                throw NoSuchElementException("Collection is one element.")
            else {
                iterator.next()
            }
        }
    }
}

