//////

/**
 * Golden ratio.
 */
val PHI: Double = (1 + sqrt(5.0)) / 2


/**
 * Calculates product of all positive (including 0) integers less than or equal [x].
 * @throws IllegalArgumentException when argument is negative
 */
fun factorial(x: Int): Long {
    require(x >= 0)
    if (x == 0) return 1
    var result = 1L
    for (i in 2..x) {
        result *= i
    }

    return result
}

/**
 * Calculates product of all positive (including 0) integers less than or equal to this value.
 * @throws IllegalArgumentException when argument is negative
 */
val Int.factorial: Long
    get() = factorial(this)


///////////
//
//  Logic
