/**
 * Performs a logical `implies` operation between this Boolean and the [other] one. Value of [other] is wrapped in
 * lambda to ensure short-circuit evaluation.
 */
infix fun Boolean.implies(other: () -> Boolean): Boolean = if (this) other.invoke() else true


