package material_components.stylesheets

import kotlinx.css.*
import kotlinx.css.properties.*
import styled.StyleSheet
import styled.getClassName
import kotlin.reflect.KCallable
import kotlin.reflect.KProperty0

const val ACCENT_COLOR_VAR = "accent_color_outlined-text-field"

object InputStyleSheet: StyleSheet("InputStyleSheet") {
    
    val outlinedTextField_container by css {
        
        height = 56.px
        minWidth = 160.px
        padding(12.px, 16.px, 14.px)
        
        boxSizing = BoxSizing.borderBox
        position = Position.relative
        display = Display.flex
        
        descendants {
            boxSizing = BoxSizing.borderBox
            fontFamily = "Roboto, Arial, sans-serif"
            cursor = Cursor.text
            
            borderColor = Colors.black__type_disabled
            
            transition += Transition("transform", 150.ms, cubicBezier(0.4, 0.0, 0.2, 1.0), 0.ms)
            transition += Transition("border-color", 15.ms, cubicBezier(0.4, 0.0, 0.2, 1.0), 0.ms)
            transition += Transition("border-width", 15.ms, cubicBezier(0.4, 0.0, 0.2, 1.0), 0.ms)
            transition += Transition("color", 150.ms, cubicBezier(0.4, 0.0, 0.2, 1.0), 0.ms)
        }
        
        hover {
            descendants {
                borderColor = Colors.black__type_high_emphasis
            }
        }
    }
    
    val outlinedTextField_container_withHelper by css {
        
        marginBottom = 24.px
    }
    
    private val outlinedTextField_areaText by css {
        
        fontSize = 16.px
        letterSpacing = 0.15.px
        lineHeight = 30.px.lh
    }
    
    val outlinedTextField_input by css {
        
        "&::-webkit-calendar-picker-indicator" {
            display = Display.none
        }
        
        "&::-webkit-outer-spin-button" {
            put("-webkit-appearance", "none")
            margin(0.px)
        }
        
        "&::-webkit-inner-spin-button" {
            put("-webkit-appearance", "none")
            margin(0.px)
        }
        
        allUnset()
        height = 100.pct
        width = 100.pct
        
        zIndex = 1
        
        color = Colors.black__type_high_emphasis
        +outlinedTextField_areaText
        
        invalid {
            color = Colors.error
            sibling(getDataName { InputStyleSheet::outlinedTextField_outerDecoration }) {
                descendants(getDataName { InputStyleSheet::outlinedTextField_labelText }) {
                    color = Colors.error
//                    put("color", "${Colors.error_s} !important")
                }
                descendants {
                    borderColor = Colors.error
//                    put("border-color", "${Colors.error_s} !important")
                }
            }
        }
        
        val active by css {
            sibling(getDataName { InputStyleSheet::outlinedTextField_outerDecoration }) {
                descendants(getDataName { InputStyleSheet::outlinedTextField_labelText }) {
                    transform {
                        translateY((-40).px)
                    }
                    
                    fontSize = 12.px
                }
                descendants(getDataName { InputStyleSheet::outlinedTextField_centerDecoration }) {
                    borderTopColor = Color.transparent
                    
                    transition += Transition("border-top-color", 150.ms, cubicBezier(0.4, 0.0, 0.2, 1.0), 0.ms)
                }
            }
        }
        
        val activeFull by css {
            sibling(getDataName { InputStyleSheet::outlinedTextField_outerDecoration }) {
                descendants {
                    borderColorVar = ACCENT_COLOR_VAR
                    borderWidth = 2.px
                    colorVar = ACCENT_COLOR_VAR
                }
            }
        }
        
        focus {
            not(":invalid") {
                +activeFull
            }
            +active
        }
        
        not(":placeholder-shown") { +active }
    }
    
    val outlinedTextField_outerDecoration by css {
        top = 0.px
        right = 0.px
        bottom = 0.px
        left = 0.px
        
        position = Position.absolute
        display = Display.flex
        
        children {
            borderTopWidth = 1.px
            borderTopStyle = BorderStyle.solid
            borderBottomWidth = 1.px
            borderBottomStyle = BorderStyle.solid
        }
    }
    
    val outlinedTextField_leftDecoration by css {
        width = 12.px
        
        borderLeftWidth = 1.px
        borderLeftStyle = BorderStyle.solid
        borderTopLeftRadius = 2.px
        borderBottomLeftRadius = 2.px
    }
    
    val outlinedTextField_centerDecoration by css {
        flex(0.0, 0.0, FlexBasis.auto)
        width = LinearDimension.auto
        padding(0.px, 4.px)
        maxWidth = 100.pct - (12.px * 2)
    }
    
    val outlinedTextField_rightDecoration by css {
        flexGrow = 1.0
        
        borderRightWidth = 1.px
        borderRightStyle = BorderStyle.solid
        borderTopRightRadius = 2.px
        borderBottomRightRadius = 2.px
    }
    
    val outlinedTextField_labelText by css {
        top = 50.pct
        transform {
            translateY((-50).pct)
        }
        
        display = Display.inlineBlock
        position = Position.relative
        
        +outlinedTextField_areaText
    }
    
    val outlinedTextField_helperText by css {
        bottom = 0.px
        transform {
            translateY(100.pct)
        }
        left = 16.px
        maxWidth = 100.pct - 32.px
        
        position = Position.absolute
        display = Display.inlineBlock
        overflow = Overflow.hidden
        textOverflow = TextOverflow.ellipsis
        
        color = Colors.black__type_medium_emphasis
        fontSize = 12.px
        letterSpacing = 0.28.px
        lineHeight = 24.px.lh
        
    }
    
    val outlinedTextField_prefixText by css {
        
        paddingRight = 2.px
        
        display = Display.inlineBlock
        
        color = Colors.black__type_medium_emphasis
        whiteSpace = WhiteSpace.pre
        +outlinedTextField_areaText
    }
    
    val outlinedTextField_postfixText by css {
        
        paddingLeft = 2.px
        
        display = Display.inlineBlock
        
        color = Colors.black__type_medium_emphasis
        whiteSpace = WhiteSpace.pre
        +outlinedTextField_areaText
    }
}
